import React, { useEffect, useState } from 'react';
import Header from './Header';
import Body from './Body';
import { wasm } from '../lib/wasm';
import BigNumber from "bignumber.js";

export default function App() {
  const [transfers, setTransfers] = useState([]);
  const [approvers, setApprovers] = useState([]);
  const [quorum, setQuorum] = useState([]);
  const [loading, setLoading] = useState(true);
  const [reload, setReload] = useState(0);
  const limit = 10;

  useEffect(() => {
    const init = async () => {
      const { data: membersData } = await wasm.query(
        process.env.REACT_APP_GROUP_ADDRESS,
        {
          list_members: {}
        }
      );
      setApprovers(membersData?.members);

      //AbsoluteCount, AbsolutePercentage, ThresholdQuorum
      const { data: quorum } = await wasm.query(
        process.env.REACT_APP_MULTISIG_ADDRESS,
        { threshold: {} }
      );
      setQuorum(quorum);
      setLoading(false);
    };
    init();
  }, []);

  // only update transfers, not quorum or members
  useEffect(() => {
    const init = async () => {
      const { data: proposalsData } = await wasm.query(
        process.env.REACT_APP_MULTISIG_ADDRESS,
        {
          list_proposals: {
            limit
          }
        }
      );
      // each proposal can see the voters details
      setTransfers(proposalsData?.proposals);
      setLoading(false);
    };
    init();
  }, [reload]);

  const nextTransfers = async () => {
    const { data: proposalsData } = await wasm.query(
      process.env.REACT_APP_MULTISIG_ADDRESS,
      {
        list_proposals: {
          limit,
          start_after: transfers.slice(-1)[0]?.id
        }
      }
    );
    // each proposal can see the voters details
    setTransfers([...transfers, ...proposalsData?.proposals]);
  };

  const createTransfer = async ({ title, description, to, amount }) => {
    try {
      setLoading(true);
      await wasm.execute(
        process.env.REACT_APP_MULTISIG_ADDRESS,
        {
          propose: {
            title,
            description,
            msgs: [
              {
                bank: {
                  send: {
                    to_address: to,
                    from_address: process.env.REACT_APP_MULTISIG_ADDRESS,
                    amount: [{ denom: 'orai', amount: new BigNumber(amount).multipliedBy(1000000).toString()}]
                  }
                }
              }
            ]
          }
        },
        { gas: 50000000 }
      );
      setReload(reload + 1);
    } catch (ex) {
      alert(ex.message);
      setLoading(false);
    }
  };

  const approveTransfer = async ({ id, type }) => {
    try {
      setLoading(true);
      // excute approve transfer with message and waiting
      switch (type) {
        case 'execute':
          await wasm.execute(process.env.REACT_APP_MULTISIG_ADDRESS, {
            execute: { proposal_id: id }
          });
          break;
        case 'vote':
          await wasm.execute(
            process.env.REACT_APP_MULTISIG_ADDRESS,
            { vote: { proposal_id: id, vote: 'yes' } },
            { gas: 4000000 }
          );
          break;
      }
      // reload page
      setReload(reload + 1);
    } catch (ex) {
      alert(ex.message);
      setLoading(false);
    }
  };

  if (loading === true) {
    return <div className="container text-center">Loading...</div>;
  }
  
  return (
    <div className="mx-5">
      <Header />
      <Body
        approvers={approvers}
        quorum={quorum}
        transfers={transfers}
        approveTransfer={approveTransfer}
        createTransfer={createTransfer}
        nextTransfers={nextTransfers}
      />
    </div>
  );
}
