import React, { useState } from "react";
import debounce from "lodash/debounce";

export default function CreateTransfer({ createTransfer }) {
  const [transfer, setTransfer] = useState();

  const debouncedUpdate = debounce(
    (obj) => setTransfer({ ...transfer, ...obj }),
    500,
    {
      trailing: true,
    },
  );

  const handleChange = (e) => {
    e.persist();
    debouncedUpdate({ [e.target.name]: e.target.value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    createTransfer(transfer);
  };

  return (
    <div className="mt-3">
      <h5>Create Transfer</h5>
      <form className="mt-4 flex-form" onSubmit={handleSubmit}>
        <div className="input-group mb-2">
          <div className="input-group-prepend">
            <div className="input-group-text">Title</div>
          </div>
          <input
            name="title"
            className="form-group form-control mb-0"
            type="text"
            placeholder="Proposal title"
            onChange={handleChange}
          />
        </div>
        <div className="input-group mb-2">
          <div className="input-group-prepend">
            <div className="input-group-text">Description</div>
          </div>
          <textarea
            name="description"
            className="form-group form-control mb-0 fullwidth"
            type="text"
            placeholder="Proposal description"
            onChange={handleChange}
          ></textarea>
        </div>
        <div className="input-group mb-2">
          <div className="input-group-prepend">
            <div className="input-group-text">Orai</div>
          </div>
          <input
            name="amount"
            className="form-group form-control mb-0"
            type="number"
            placeholder="eg: 1.5"
            onChange={handleChange}
          />
        </div>
        <div className="input-group mb-2">
          <div className="input-group-prepend">
            <div className="input-group-text">To</div>
          </div>
          <input
            name="to"
            className="form-group form-control mb-0 w-75"
            type="text"
            placeholder="orai123xxx..."
            onChange={handleChange}
          />
        </div>

        <button className="btn btn-info mt-1 float-right">
          Submit Transfer
        </button>
      </form>
    </div>
  );
}
