import React from 'react';
import ReactJson from 'react-json-view';

export default function Approvers({ approvers, quorum }) {
  return (
    <div className="w-100">
      <h5 className="mt-3">Approvers Address</h5>
      <table className="my-3 table table-sm">
        <thead>
          <tr>
            <th>Address</th>
            <th>Weight</th>
          </tr>
        </thead>
        <tbody>
          {approvers.map((approver, index) => {
            return (
              <tr key={index}>
                <td> {approver.addr}</td>
                <td>{approver.weight}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <div className="mt-3 font-italic">
        Quorum:{' '}
        <span className="font-weight-bold">
          <ReactJson
            src={quorum}
            name={false}
            displayObjectSize={false}
            displayDataTypes={false}
            displayArrayKey={false}
          />
        </span>
      </div>
    </div>
  );
}
