import React from "react";
import Votes from "./Votes";
import BigNumber from "bignumber.js";

// 	   Pending = 1,
//     /// you can vote on this
//     Open = 2,
//     /// voting is over and it did not pass
//     Rejected = 3,
//     /// voting is over and it did pass, but has not yet executed
//     Passed = 4,
//     /// voting is over it passed, and the proposal was executed
//     Executed = 5,
const TransferAction = ({ status, id, handler }) => {
  switch (status) {
    case "open":
      return (
        <button
          className="btn btn-info"
          onClick={() => handler?.({ type: "vote", id })}
        >
          Vote
        </button>
      );
    case "passed":
      return (
        <button
          className="btn btn-warning"
          onClick={() => handler?.({ type: "execute", id })}
        >
          Execute
        </button>
      );
    default:
      return null;
  }
};

export default function Transfers({
  transfers,
  nextTransfers,
  approveTransfer,
}) {
  return (
    <div className="mt-3 w-100">
      <h5 className="text-center mt-3">Transfers - Approvals</h5>
      <table className="my-3 table table-sm">
        <thead>
          <tr>
            <th>Id</th>
            <th>Amount</th>
            <th>To</th>
            <th>Status</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {transfers.map(({ id, msgs, status }) => {
            const data = msgs[0]?.bank.send;
            if (!data) return;

            const { to_address: to } = data;
            const { amount, denom } = data.amount[0];
            return (
              <tr key={id}>
                <td>{id}</td>
                <td>
                  {BigNumber(amount).dividedBy(1000000).toFormat()}{" "}
                  <small className="font-weight-bold">
                    {denom.toUpperCase()}
                  </small>
                </td>
                <td>
                  {to}
                  <div className="font-italic">
                    Votes:{" "}
                    <span className="font-weight-bold">
                      <Votes id={id} />
                    </span>
                  </div>
                </td>
                <td>
                  <span className="text-capitalize">{status}</span>
                </td>
                <td>
                  <TransferAction
                    id={id}
                    status={status}
                    handler={approveTransfer}
                  />
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <nav>
        <ul className="pagination">
          <li className="page-item">
            <button className="btn btn-link" onClick={nextTransfers}>
              Next
            </button>
          </li>
        </ul>
      </nav>
    </div>
  );
}
